.todo-list-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
}
.todo-list-item__label {
  font-size: 1.2rem;
  font-weight: 300;
}
.todo-list-item__label.hot {
  font-weight: 700;
  color: tomato;
}
.todo-list-item__label.done {
  text-decoration: line-through;
}
.todo-list {
  margin-top: 1rem;
}
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: stretch;
          align-items: stretch;
  height: 100vh;
  min-height: 500px;
  font-family: 'Oswald', sans-serif;
  padding: .5rem 1rem 0;
}
.main {
  -webkit-flex: 1 1;
          flex: 1 1;
} 
.app > * {
  width: 100%;
}

